.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
}
svg {
  font-size: 100px;
}
.profile-pic {
  max-width: 35%;
}
@media only screen and (min-width: 1000px) {
  .profile-pic {
    max-width: 15%;
  }
}
@media only screen and (max-width: 800px) {
  svg {
    font-size: 76px;
  }
}
@media only screen and (max-width: 600px) {
  svg {
    font-size: 48px;
  }
}
